.sidebar{
    flex: 1;
    height: calc(100vh -50px);
    background-color: #f8fbfd;
    position: sticky;
    top: 50;
}

.sidebarcontents{
    padding: 20px;
    color: #4b4a4a;

}

.Menu{
    margin-bottom: 10px;
}

.Sidebar-heading{
    font-size: 15px;
    color: #4b4a4a;
}

.Sidebar-lists{
    list-style: none;
    padding: 10px;
}

.sidebaritems{
    padding: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 5px;
}

/* .sidebaritems.active, .sidebaritems:hover{
    text-decoration: underline;
    background-color: #fbfbfc;
} */


.sidebaritems-icon{
    margin-right: 10px;
}

a{
    text-decoration: none !important;
    color: #4b4a4a !important;
}

a:hover{
    color: #2eb3c3 !important;
}

a:focus{
    color: blueviolet !important;
}