.Register{
    flex: 4;
}
.NewSubscriberForm{
    display: flex;
    flex: 2;
    flex-wrap: wrap;
}

.FormItems{
    width: 300px;
    display: flex;
    flex-direction: column;
    margin-top: 15px;
    margin-right: 15px;
}

.FormItems > label{
    margin-bottom: 15px;
    font-size: 16px;
}

.FormItems > input{
    height: 10px;
    padding: 8px;
    border: 1 px solid gray;
    border-radius: 3px;;
}

.RegisterNewSubscriber{
    height: 35px;
    margin-top: 50px;
    border: none;
    cursor: pointer;
    background-color: rgb(125, 207, 199);
    color: rgb(110, 110, 108);
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 5px;
}

.NewSubscriberHeading{
    color: #4b4a4a;
}