.user{
    flex: 4;
    padding: 15px;
}

.userContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.RegisterSubscriber{
    height: 35px;
    margin-top: 50px;
    border: none;
    cursor: pointer;
    background-color: rgb(125, 207, 199);
    color: rgb(110, 110, 108);
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 5px;
}

.Userheading{
    color: #4b4a4a;
}

.UserContents{
    display: flex;
    margin-top: 25px;
    flex: 1;
}

.ShowUser{
    flex: 1;
    padding: 15px;
    box-shadow: 3px 7px 5px -1px rgba(0,0,0,0.22);
    -webkit-box-shadow: 3px 7px 5px -1px rgba(0,0,0,0.22);
    -moz-box-shadow: 3px 7px 5px -1px rgba(0,0,0,0.22);
}

