.Info{
    width: 100%;
    display: flex;
    justify-content: space-between;

}

.InfoItem{
    flex: 1;
    margin: 0px 20px;
    padding: 30px;
    background-color: white;
    margin-top: 20px;
    align-items: center;
    box-shadow: 3px 7px 5px -1px rgba(0,0,0,0.22);
    -webkit-box-shadow: 3px 7px 5px -1px rgba(0,0,0,0.22);
    -moz-box-shadow: 3px 7px 5px -1px rgba(0,0,0,0.22);
}

.Icons-Numbers{
    padding-left: 10px;
}

.InfoSubscribers{
    display: flex;
    align-items: center;
    padding-top: 10px;
}

.icons{
    margin-right: 10px;
}

.InfoTitle{
    color: #4b4a4a;
    font-weight: bold;
    font-size: 18px;
}